@charset "UTF-8";
:root {
  --clip: left center;
  --size: 0;
  --background-thumb: #2b3769;
  --scrollbar-thumb-bgcolor: #757575;
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline:hover::before {
    width: 47%;
  }
  .underline:hover::after {
    width: 47%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline-w:hover::before {
    width: 47%;
  }
  .underline-w:hover::after {
    width: 47%;
  }
  .underline-w::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
.company-team {
  background: #2b3769;
  padding: 13rem 50px;
}
@media screen and (max-width: 1440px) {
  .company-team {
    padding: 13rem 30px;
  }
}
@media screen and (max-width: 690px) {
  .company-team {
    padding: 60px 30px;
  }
}
@media screen and (max-width: 375px) {
  .company-team {
    padding: 50px 10px;
  }
}
.company-team__body {
  position: relative;
}
.company-team__row {
  position: relative;
  z-index: 9;
}
.company-team__title {
  margin-bottom: 70px;
  color: #ffffff;
  text-align: center;
}
@media screen and (max-width: 690px) {
  .company-team__title {
    margin-bottom: 40px;
  }
}
.company-team__content {
  display: grid;
  gap: 40px;
  grid-template-columns: repeat(3, 1fr);
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .company-team__content {
    gap: calc(20px + 40 * (100vw - 1440px) / 480);
  }
}
@media screen and (max-width: 1440px) {
  .company-team__content {
    gap: 20px;
  }
}
@media screen and (max-width: 1164px) {
  .company-team__content {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 568px) {
  .company-team__content {
    grid-template-columns: repeat(1, 1fr);
  }
}
.company-team__column {
  border-radius: 30px;
  background: #ffffff;
}
@media screen and (max-width: 690px) {
  .company-team__column {
    border-radius: 15px;
  }
}

.team-card__body {
  padding: 30px;
}
@media screen and (max-width: 768px) {
  .team-card__body {
    padding: 20px;
  }
}
@media screen and (max-width: 690px) {
  .team-card__body {
    padding: 15px;
  }
}
@media screen and (max-width: 490px) {
  .team-card__body {
    padding: 10px;
  }
}
.team-card__header {
  display: flex;
  gap: 10px;
  justify-content: space-between;
}
@media screen and (max-width: 568px) {
  .team-card .team-card__content {
    margin-top: -12px;
  }
}
.team-card__image {
  width: 232px;
  border-radius: 20px;
  overflow: hidden;
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .team-card__image {
    width: calc(117px + 115 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .team-card__image {
    width: 117px;
  }
}
.team-card__image img {
  width: 100%;
  object-fit: contain;
  filter: grayscale(0.3);
}
@media screen and (max-width: 768px) {
  .team-card__image {
    border-radius: 10px;
  }
}
.team-card__logo {
  width: 40px;
  height: 40px;
}
.team-card__post {
  margin-bottom: 5px;
  font-size: 16px;
  font-weight: 400;
  line-height: 125%; /* 20px/16 */
  text-align: right;
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .team-card__post {
    font-size: calc(12px + 4 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .team-card__post {
    font-size: 12px;
  }
}
.team-card__name {
  font-size: 35px;
  font-weight: 400;
  line-height: 122.857143%; /* 43px/35 */
  text-align: right;
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .team-card__name {
    font-size: calc(20px + 15 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .team-card__name {
    font-size: 20px;
  }
}

.elipsis-cont {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
}
.elipsis-cont__elipse-10 {
  position: absolute;
  width: 1195px;
  height: 852px;
  border-radius: 30%;
  bottom: 0;
  right: -10%;
  filter: blur(241.65px);
  background: rgba(172, 186, 189, 0.379);
}